import React, { useState } from 'react';
import PropTypes from 'prop-types';
import myContext from './contextAPI';

function Provider({ children }) {
  const [planets, setPlanets] = useState([]);
  const [planetsComplete, setPlanetsComplete] = useState([]);
  const [name, setName] = useState('');
  const [filtersValues, setFiltersValues] = useState([]);
  const [order, setOrder] = useState({ column: 'name', sort: 'ASC' });
  const planetsDescriptions = {
    Alderaan: "Alderaan, located in the Core Worlds, was a terrestrial planet covered by mountains. During the last decades of the Galactic Republic, the planet was ruled by Queen Breha Organa and represented in the Galactic Senate by her husband, Senator Bail Organa. After the rise of Sheev Palpatine's Galactic Empire, Alderaan was instrumental in establishing the Rebel Alliance, a movement that sought to restore the ancient values ​​of the Old Republic. Due to its connections to the rebel movement, the planet was destroyed when the Great Moff Wilhuff Tarkin decided to test the Death Star superlaser, a moon-sized war station developed by the Empire. Among the few survivors of the disaster was Princess Leia Organa, adopted daughter of Bail and Breha Organa and heiress to the royal house of Alderaan.",
    Tatooine: "Tatooine was a desert planet located in the Outer Rim Territories. The planet was part of a binary solar system. Due to the lack of surface water, many of the planet's residents drained water from the atmosphere through moisture farms. Tatooine was the birthplace of Anakin Skywalker, The Chosen One, and his son, Luke Skywalker. Anakin's former master, Obi-Wan Kenobi, also lived on the planet after the Clone Wars, adopting the name Ben Kenobi. Clan Hutt maintained a presence on Tatooine as well, with Jabba the Hutt having a palace in the desert. Tatooine gained importance to the Galactic Empire during the Galactic Civil War when an escape pod carrying two droids that possessed secret information was launched from the starship Tantive IV and landed on the sandy world of the Outer Rim.",
    "Yavin IV": "Yavin 4 was a jungle-covered moon that orbited around the red-colored Gas Giant Yavin. At some point in the Galactic Civil War, a group of fighters came out to destroy the Death Star. After the Battle of Yavin, the Alliance moved their base to Hoth.",
    Hoth: "Hoth was a remote icy planet, the sixth in a star system of the same name. There was Echo Base, temporary headquarters of the Rebel Alliance until the Galactic Empire found them, triggering a major confrontation known as the Battle of Hoth.",
    Dagobah: "Dagobah was a Planet in the Dagobah System, and one of the purest places in the galaxy within the Force. A world full of swamps and forests, it served as an exile for Grand Jedi Master Yoda after the destruction of the Jedi Order. During his exile, Luke Skywalker learned about the ways of the Force, later right there in Dagobah Luke witnessed the death of Yoda, who joined with the Force.",
    Bespin: "Bespin was a huge Gaseous Giant located in Sector Anoat, a desolate sector of the Galaxy. Bespin had several moons and its gaseous mass contained some habitable atmosphere. Its layers were the source of Tibanna, a rare gas, which was collected and refined at various mining complexes, including Cloud City, a metropolis suspended in the planet's huge undulating clouds. The Ugnaught city of Ugnorgrad was also located on the planet, being ruled by King Ozz after the Battle of Endor. During the Galactic Civil War, Bespin remained aloof, but that didn't stop the Galactic Empire from taking possession of the planet. However the defeat of the Empire at Endor, the gas giant and its mining regained their freedom.",
    Endor: "Endor (also known as Forest Moon of Endor or Sanctuary Moon) was a small forest moon that orbited the Outer Rim planet of the same name that was home to the species Ewok and Yuzzum.",
    Naboo: "Naboo was a bountiful planet in the Chommell sector of the Mid Rim, close to the border of the Outer Rim Territories. It was home to the Gungan species and to a population of humans known as the Naboo. Naboo was pushed to the forefront of galactic politics as the birthplace of the Dark Lord of the Sith Sheev Palpatine, who served as its representative in the Senate of the Galactic Republic.",
    Coruscant: "Coruscant was a planet that served as the capital of the Galactic Republic and the Galactic Empire and as the home planet of the human species.",
    Kamino: "Kamino was an aquatic planet located in an extragalactic star system that circled south of the Rishi Labyrinth. It was inhabited by the Kaminoans—a tall, elegant race of beings who were considered a mysterious species that tended to take shelter. They were also known for their cloning technology which ultimately led to the creation of the Galactic Republic clone army.",
  }
  const contextValue = {
    planets,
    planetsComplete,
    planetsDescriptions,
    filters: {
      filterByName: { name },
      filterByNumericValues: filtersValues,
      order,
    },
    setName,
    setPlanets,
    setPlanetsComplete,
    setFiltersValues,
    setOrder,
  };

  return (
    <myContext.Provider value={ contextValue }>
      { children }
    </myContext.Provider>
  );
}

Provider.propTypes = {
  children: PropTypes.objectOf(PropTypes.any).isRequired,
};

export default Provider;
