import React from 'react';
import Filter from '../components/Filter';
import Footer from '../components/Footer';
import Header from '../components/Header';
import Table from '../components/Table';
import myContext from '../context/contextAPI';
import { requestPlanets } from '../serviceAPI';

class Home extends React.Component {
  componentDidMount() {
    const { setPlanets, setPlanetsComplete } = this.context;
    requestPlanets().then((response) => {
      setPlanetsComplete(response.results);
      response.results.forEach((planet) => {
        delete planet.terrain;
        delete planet.gravity;
        delete planet.residents;
        delete planet.films;
        delete planet.edited;
        delete planet.url;
        delete planet.created;
      });
      setPlanets(response.results);
    });
    requestPlanets().then((response) => setPlanetsComplete(response.results));
  }

  render() {
    return (
      <div>
        <Header />
        <Filter />
        <Table />
        <Footer />
      </div>
    );
  }
}

Home.contextType = myContext;

export default Home;
