import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import myContext from '../context/contextAPI';
import { requestResident } from '../serviceAPI';

class PlanetDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      residents: [],
    } 
  }
  componentDidMount() {
    const { planet } = this.props.match.params;
    const { planetsComplete } = this.context;
    const { residents } = planetsComplete.find((item) => item.name === planet);
    residents.forEach(
      (resident) => requestResident(resident).then(
        (response) => this.setState((state) => ({ residents: [...state.residents, response] }))));
    window.scroll(0,0)
  }

  renderInfo() {
    const { planet } = this.props.match.params;
    const { planetsComplete, planetsDescriptions } = this.context;
    const planetInfo = planetsComplete.find((item) => item.name === planet);
    return (
      <div className="container-details">
        <div className="container-description">
          <img className="imgPlanet-details" src={ require(`../images/planets/${planet}.jpg`) } alt="planet"></img>
          <div className="container-info">
            <h2 className="title-details">{ planetInfo.name === 'Yavin IV' ? 'Yavin 4' : planetInfo.name }</h2>
            <p className="text-description">{ planetsDescriptions[`${ planet }`] }</p>
          </div>
        </div>
        <div className="statistic-planet">
          <div className="lineInfo">
            <p className="title-statistic">Terrain</p>
            <p className="info-statistic">{ planetInfo.terrain }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Climate</p>
            <p className="info-statistic">{ planetInfo.climate }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Gravity</p>
            <p className="info-statistic">{ planetInfo.gravity }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Surface Water</p>
            <p className="info-statistic">{ planetInfo.surface_water }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Orbital Period</p>
            <p className="info-statistic">{ planetInfo.orbital_period }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Rotation Period</p>
            <p className="info-statistic">{ planetInfo.rotation_period }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Diameter</p>
            <p className="info-statistic">{ planetInfo.diameter }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Population</p>
            <p className="info-statistic">{ planetInfo.population }</p>
          </div>
        </div>
      </div>
    );
  }

  residentCard(resident) {
    const { name, gender, height, hair_color, eye_color, skin_color} = resident;
    return (
      <div className="container-resident-card">
        <img className="imgResident" src={ require(`../images/residents/${name}.jpg`)} alt="resident"></img>
        <div className="container-resident-info">
          <h3 className="nameResident">{ name }</h3>
          <div className="lineInfo">
            <p className="title-statistic">Gender</p>
            <p className="info-statistic">{ gender }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Height</p>
            <p className="info-statistic">{ height }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Eye Color</p>
            <p className="info-statistic">{ eye_color }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Hair Color</p>
            <p className="info-statistic">{ hair_color }</p>
          </div>
          <div className="lineInfo">
            <p className="title-statistic">Skin Color</p>
            <p className="info-statistic">{ skin_color }</p>
          </div>
        </div>
      </div>
    );
  }

  renderResidents() {
    const { residents } = this.state;
    return (    
      <div className="container-residents">
        <h2 className="title-details">Residents</h2>
        <div className="container-cards">
          { residents.map((resident) => this.residentCard(resident)) }
        </div>
      </div>
    );
  }

  render() {
    const { residents } = this.state;
    return (
      <div>
        <Header />
        { this.renderInfo() }
        { residents.length > 0 && this.renderResidents() }
        <Footer />
      </div>
    );
  }
}

PlanetDetails.contextType = myContext;

export default PlanetDetails;
